import React, { useState, useContext } from 'react';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';

import { CalendarContext } from '../../context.jsx';
import { FieldComponent } from '../../form';

import './entry-form.scss';

const EntrySchema = Yup.object().shape({
	title: Yup.string().required('required').max(80),
	description: Yup.string().length(1200),
});

export default function EntryForm({ entry, onSubmit, onCancel }) {
	const { calendar } = useContext(CalendarContext);

	if(!entry) {
		entry = {
			title: '',
			description: ''
		};
	}

	return (
		<div className='entry-form-wrap justify-centre absolute top padding-vertical-3'>
			<div className='entry-form-container relative background-color-white padding-top-small box-shadow-large border-radius'>
				<div className='padding-all'>
					<Formik
						initialValues={entry}
						validationSchema={EntrySchema}
						onSubmit={onSubmit}
						render={({ errors, validateForm }) => (
							<Form autoComplete='off' className='entry-form relative'>
								<Field component={FieldComponent} name='title' placeholder='Title' />
								<Field name='description' component='textarea' placeholder='Description' className='form-field margin-top border-bottom-mid-grey color-mid-grey line-height-copy width-100' />
							</Form>
						)}
					/>
				</div>
				<footer className='padding-horizontal padding-bottom clearfix'>
					<button className='button button--blue button--shift-on-hover button--shadow-on-hover float-right'>Create event</button>
					{onCancel ?<button onClick={onCancel} className='button button--grey-ghost float-right font-weight-medium color-dark-grey margin-right-medium'>Cancel</button> : null}
				</footer>
			</div>
		</div>
	);
}
