import React from 'react';
import posed, { PoseGroup } from 'react-pose';
import classnames from 'classnames';

const ErrorContainer = posed.div({
	enter: { height: 'auto' },
	exit: { height: 0 }
});

const FieldComponent = ({
	field,
	className,
	form: { touched, errors, values },
	...props
}) => (
	<div className=''>
		<input className={classnames('calendar-form-field form-field line-height-copy font-size-medium max-width-100 min-width-100', className)} type="text" {...field} {...props} />

		<PoseGroup flipMove={false}>
			{(touched[field.name] && errors[field.name]) ? (
				<ErrorContainer key='errorContainer' className='overflow-hidden'><p className='font-size-medium color-muted-grey margin-left-small margin-top-medium margin-bottom-small'>{errors[field.name]}</p></ErrorContainer>
			) : null}
		</PoseGroup>
	</div>
);

export default FieldComponent;
